import React,{useState, useEffect} from 'react';

import { Map, MapMarker, Circle, ZoomControl,Roadview, RoadviewMarker } from 'react-kakao-maps-sdk';
import home from '../asset/img/home.png';
import orangeMarker from '../asset/img/marker-orange.png';
import flag from '../asset/img/flag.png';
import dummy, { travelList } from '../asset/js/dummy'
//etc
const {kakao} = window;
export default function JejuMap(){

  const [info, setInfo] = useState()
  const [markers, setMarkers] = useState([])
  const [map, setMap] = useState()
  const [inputValue, setInputValue] = useState("    263-15");
  const [draggable, setDraggable] = useState(false);



  useEffect(() => {
    if (!map) return
    const ps = new kakao.maps.services.Places()

    ps.keywordSearch(inputValue, (data, status, _pagination) => {
      if (status === kakao.maps.services.Status.OK) {
        //       
        // LatLngBounds   
        const bounds = new kakao.maps.LatLngBounds()
        let markers = []

        for (var i = 0; i < data.length; i++) {
          // @ts-ignore
          markers.push({
            position: {
              lat: data[i].y,
              lng: data[i].x,
            },
            content: data[i].place_name,
          })
          // @ts-ignore
          bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x))
        }
        setMarkers(markers)

        //       
        map.setBounds(bounds)
      }
    })

    //  
  //   if (navigator.geolocation) {
    
  //     // GeoLocation    
  //     navigator.geolocation.getCurrentPosition(function(position) {
          
  //         var lat = position.coords.latitude, // 
  //             lon = position.coords.longitude; // 
          
  //         var locPosition = new kakao.maps.LatLng(lat, lon), //    geolocation   
  //             message = '<div style="padding:5px;">!</div>'; //   
          
  //         //   
  //         displayMarker(locPosition, message);
              
  //       });
      
  // } else { // HTML5 GeoLocation         
      
  //     var locPosition = new kakao.maps.LatLng(33.450701, 126.570667),    
  //         message = 'geolocation  ..'
          
  //     displayMarker(locPosition, message);
  // }
  
  //     
  function displayMarker(locPosition, message) {
  
      //  
      var marker = new kakao.maps.Marker({  
          map: map, 
          position: locPosition
      }); 
      
      var iwContent = message, //   
          iwRemoveable = true;
  
      //  
      var infowindow = new kakao.maps.InfoWindow({
          content : iwContent,
          removable : iwRemoveable
      });
      
      //    
      infowindow.open(map, marker);
      
      //    
      map.setCenter(locPosition);      
  }  
// 

  }, [map,inputValue])
  return(
    <div className="second_section sections">
          <Map //   Container
      center={{
        lat: 33.339120415606985, 
        lng: 126.79790619306574,
      }}
      className="jeju_map"
      level={10}
      onCreate={setMap}
      // draggable={draggable}
    >

      {/*    */}
      {travelList.map((marker) => (
        <MapMarker
          // key={`marker-${marker.content}-${marker.position.lat},${marker.position.lng}`}
          position={marker.position}
          onClick={() => setInfo(marker)}
          image={{
            src: flag, //  
            size: {
              width: 14,
              height: 14,
            },
          }}
        >
          {info &&info.id === marker.id && (
            <div  className="marker_info"
              onClick={()=>{
                // document.location.href('www.naver.com');
                // window.location,href=`https://map.naver.com/v5/search/${marker.content}/place`
                window.open(`https://map.naver.com/v5/search/${marker.name?.replace(" ","%20")}`,'','width=800, height=700, toolbar=no, menubar=no, scrollbars=no, resizable=yes');return false;
              }}
            >
              {marker.name}
              
            </div>
          )}
        </MapMarker>
      ))}


      {/* home@@@@@ */}
      <MapMarker
        position={{
          lat: 33.339120415606985, 
          lng: 126.79790619306574,
        }}
        image={{
          src: home, //  
          size: {
            width: 22,
            height: 22,
          },
          // options: {
          //   offset: {
          //     x: 27,
          //     y: 69,
          //   },
          // },
        }}
      />

    {/*   */}
    {markers.map((marker) => (
        <MapMarker
          key={`marker-${marker.content}-${marker.position.lat},${marker.position.lng}`}
          position={marker.position}
          onClick={() => setInfo(marker)}
          image={{
            src: orangeMarker, //  
            size: {
              width: 14,
              height: 21,
            },
            // options: {
            //   offset: {
            //     x: 27,
            //     y: 69,
            //   },
            // },
          }}
        >
          {info &&info.content === marker.content && (
            <div  className="marker_info"
              onClick={()=>{
                // document.location.href('www.naver.com');
                // window.location,href=`https://map.naver.com/v5/search/${marker.content}/place`
                window.open(`https://map.naver.com/v5/search/${marker.content?.replace(" ","%20")}`,'','width=800, height=700, toolbar=no, menubar=no, scrollbars=no, resizable=yes');return false;
              }}
            >
              {marker.content}
              
            </div>
          )}
        </MapMarker>
      ))}

                {/* <ZoomControl style={{top:'123px'}}/> */}
    </Map>


    <input 
      type="text"
      className="jeju_search_input"
      onChange={(e)=>{
        setInputValue("" + e.target.value);
      }}
      placeholder=" ex) , "
    />
    <div className="map_down_btn" onClick={(e)=>{
      // window.scrollTo({
      //   top: 300,
      //   behavior: "smooth",
      // })
      window.scrollTo({top:300,behavior:'smooth'});
    }}
    ></div>

    </div>
  );
}
